w{width:48px !important;height:48px !important;border-radius:30px;box-shadow:0 4px 8px 0 rgba(96,97,112,0.16),0 0 2px 0 rgba(40,41,61,0.04) !important;background-color:#fff !important}.faq{box-shadow:0 8px 16px 0 rgba(96,97,112,0.16),0 2px 4px 0 rgba(40,41,61,0.04);background-color:#ffffff}.faq-item{margin-top:'30px';background:'#f7f9fc';height:'320px';padding:'30px';border-radius:'0px 50px 50px 0px'}.slick-prev{left:-50px !important;color:#333 !important;font-size:14px !important}.slick-next{right:-50px !important;color:#333 !important;font-size:14px !important}body,.ant-layout{background:#fff !important}.main-menu{position:fixed;z-index:1;top:0 !important;width:100%;background:#fff !important}.main-menu .ant-menu li a{padding:0px 15px}.ant-drawer-body{padding:50px 20px 0px 20px}.ant-menu-inline,.ant-menu-vertical,.ant-menu-vertical-left{border:0px !important}.lang-active{color:#1890ff !important}.ant-layout-header{background:rgba(255,255,255,0.98);box-shadow:0px 6px 20px rgba(0,0,0,0.06);pointer-events:auto}.container{background-color:#2c3e50;display:flex;flex-direction:column;flex:1 1 100%;justify-content:flex-start;align-items:center;min-height:100vh}.container__top{margin:100px}.container__middle{text-align:center;display:flex;flex:1 1 100%;width:100%;justify-content:flex-start}.container__middle__left{display:flex;flex:1 1 1%}.container__middle__left__buttons{background:#f9da2e;border-radius:8px;font-family:Arial;font-weight:700;font-size:25px;color:#4b6c8d;letter-spacing:0;width:55px;height:236px;margin-left:15px;padding:10px 3px;display:flex;flex-direction:column;justify-content:space-around;color:#4b6c8d}.container__middle__left__buttons .button{cursor:pointer}.container__middle__left__buttons .active{color:#2c3e50}.container__middle__right{display:flex;flex-direction:column;flex:1 1 100%}.container__top_text{font-size:110px;font-family:Arial, Helvetica, sans-serif;color:#ffffff;margin-bottom:10px}

